<template>
    <el-container class="container">
        <el-header class="header"></el-header>
        <el-container>
            <el-aside class="aside">
                <el-menu
                        :default-active="index"
                        class="el-menu-vertical-demo"
                        :router="true"
                >
                    <el-menu-item index="index" :route="{path:'/'}"><i class="el-icon-menu"></i><span></span>
                    </el-menu-item>
                    <el-submenu index="1">
                        <template slot="title">
                            <i class="el-icon-star-on"></i>
                            <span></span>
                        </template>
                        <el-menu-item index="workslist" :route="{path:'/workslist'}"></el-menu-item>
                        <el-menu-item index="articlelist" :route="{path:'/articlelist'}"></el-menu-item>
                        <el-menu-item index="videoslist" :route="{path:'/videoslist'}"></el-menu-item>
                        <el-menu-item index="recruitmentlist" :route="{path:'/recruitmentlist'}"></el-menu-item>
                        <el-menu-item index="resumelist" :route="{path:'/resumelist'}"></el-menu-item>
                    </el-submenu>
                    <el-submenu index="2">
                        <template slot="title">
                            <i class="el-icon-star-on"></i>
                            <span></span>
                        </template>
                        <el-menu-item index="userlist" :route="{path:'/userlist'}"></el-menu-item>
                        <el-menu-item index="companyReal" :route="{path:'/companyReal'}"></el-menu-item>
                    </el-submenu>
                    <el-submenu index="3">
                        <template slot="title">
                            <i class="el-icon-star-on"></i>
                            <span></span>
                        </template>
                        <el-menu-item index="interviewlist" :route="{path:'/interviewlist'}"></el-menu-item>
                    </el-submenu>
                    <el-submenu index="4">
                        <template slot="title">
                            <i class="el-icon-star-on"></i>
                            <span></span>
                        </template>
                        <el-menu-item index="testQuestion" :route="{path:'/testQuestion'}"></el-menu-item>
                    </el-submenu>
                    <el-submenu index="5">
                        <template slot="title">
                            <i class="el-icon-star-on"></i>
                            <span></span>
                        </template>
                        <el-menu-item index="noticeList" :route="{path:'/noticeList'}"></el-menu-item>
                        <el-menu-item index="noticeAdd" :route="{path:'/noticeAdd'}"></el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-main>
                <slot></slot>
            </el-main>
        </el-container>
    </el-container>

</template>

<script>
    export default {
        name: "basicLayout",
        props: ["index"]
    }
</script>

<style scoped>
    .container {
        background:#F3F3F3;
        height: 100vh;
    }

    .header {
        height: 100px;
        background: #409EFF;
        line-height: 60px;
        font-size: 22px;
        font-weight: bold;
        text-align: center;
    }

    .aside {

        background: #fff;

    }
</style>


