<template>
    <basic-layout index="companyReal">
        <el-table
                :data="tableData"
                :stripe="true"
                :border="true"
                v-loading="loading"
                style="width: 100%">
            <el-table-column
                    align="center"
                    prop="name"
                    label=""
                    >
            </el-table-column>
            <!--<el-table-column
                    align="center"
                    label=""
                    prop="avatar"
                    >
                <template slot-scope="scope">
                    <img :src="scope.row.avatar" alt="" style="height: 50px; width: 50px; border-radius: 50%; background:no-repeat center/100% auto">
                </template>
            </el-table-column>-->
            <el-table-column
                    align="center"
                    prop="industry"
                    label=""
            >
            </el-table-column>
            <el-table-column
                    align="center"
                    prop="typeOfEntenterprise"
                    label=""
            >
            </el-table-column>
            <el-table-column
                    align="center"
                    prop="size"
                    label=""
                    width="200"
                    >
            </el-table-column>
            <el-table-column
                    align="center"
                    prop="location"
                    label=""
                    >
            </el-table-column>
            <el-table-column
                    align="center"
                    prop="description"
                    :show-overflow-tooltip="true"
                    label="">
            </el-table-column>
            <el-table-column
                        align="center"
                        prop="introduction"
                        :show-overflow-tooltip="true"
                        label="">
            </el-table-column>
            <el-table-column
                        align="center"
                        prop="website"
                        label="">
            </el-table-column>
            <el-table-column
                    width="200"
                    align="center"
                    label="">
                <template slot-scope="scope">
                    <el-button type="primary"  plain ><router-link :to="{path:'/companyRealshow/'+scope.row.cid}"></router-link></el-button>
                    <!--<el-button type="danger"  plain @click="del(scope.row.cid)"></el-button>-->
                </template>
            </el-table-column>
        </el-table>
    </basic-layout>

</template>

<script>
    import basicLayout from "../basicLayout";
    export default {
        name: "companyReal",
        components:{
            "basic-layout":basicLayout
        },
        data() {
            return {
                tableData: [],
                loading:true
            }
        },
        methods:{
            del(id){

                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.$http.get("https://www.easy-mock.com/mock/5b18ad9ec6b9b923a614ec23/project/delUser?id="+id).then(res=>{
                       if(res.body.data.state==="success"){
                           this.$message({
                               type: 'success',
                               message: '!'
                           });
                           this.tableData=this.tableData.filter(ele=>ele.uid!==id);
                       }else{
                           this.$message({
                               type: 'error',
                               message: ''
                           });
                       }
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
                    })
                }
        },
        mounted() {
            this.$http.get("https://www.easy-mock.com/mock/5b18ad9ec6b9b923a614ec23/project/getCompanys").then(res=> {
                if (res.status === 200) {
                    this.tableData = res.body.data;
                    this.$message({
                        message: "",
                        type: "success"
                    });
                    this.loading=false;
                } else {
                    this.$message({
                        message: "",
                        type: "error"
                    })
                }
            })
        }
    }
</script>

<style scoped>

</style>


